import React, { Component } from 'react';
import Select from 'react-select';
import axios from 'axios';
import 'bootstrap/dist/css/bootstrap.min.css';

export default class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      selectOptions : [],
      src: "",
      dest: ""
    };
  }

  async getOptions(){
    const res = await axios.get('http://localhost:8081/places')
    const data = res.data;

    const options = data.map(d => ({
    "value" : d.city,
    "label" : d.city
    }))

    this.setState({selectOptions: options})

  }

  handleChangeSrc(e){
      //console.log(e);
      this.setState({src:e.value})
      //console.log(this.state);
  }

  handleChangeDest(e){
      //console.log(e);
      this.setState({dest:e.label})
      //console.log(this.state);
  }

  handleChange = e => {
    const { name, value } = e.target;
    this.setState({
      [name]: value
    });
  };

  handleSubmit = e => {
    e.preventDefault();

    const { src, dest, choice } = this.state;

    const route = {
      src,
      dest,
      choice
    };

    axios
      .post('http://localhost:8081/find-route', route)
      .then(() => console.log('Route to be found out'))
      .catch(err => {
        console.error(err);
      });
  };

  componentDidMount(){
    this.getOptions()
  }


  render() {
    //console.log(this.state.selectOptions)
    return (
      <form onSubmit={this.handleSubmit}>
        <div className="container">
            <div className="row">
                <div className="col-sm-4">
                    <Select options={this.state.selectOptions} onChange={this.handleChangeSrc.bind(this)} placeholder="Select Source" />
                </div>
                <div className="col-sm-4"></div>
                <div className="col-sm-4">
                    <Select options={this.state.selectOptions} onChange={this.handleChangeDest.bind(this)} placeholder="Select Destination" />
                </div>
            </div>
        </div>
        <div className="container co" id="btnp">
          <div className="row">
              <div className="col-sm-2"></div>
              <div className="col-sm-2"></div>
              <div className="col-sm-2">
                <input type="radio" name="choice" value="Fastest" onChange={this.handleChange} /> Fastest
              </div>
              <div className="col-sm-2">
              <input type="radio" name="choice" value="Cheapest" onChange={this.handleChange} /> Cheapest
              </div>
              <div className="col-sm-2"></div>
              <div className="col-sm-2"></div>
          </div>
        </div>
        <div className="container">
          <button className="btn btn-primary" type="submit">Find Route</button>
        </div>
      </form>
    );
  }
}